import React, { Component } from 'react'
import styled from 'styled-components'
import moment from 'moment'

const DayView = styled.div`
  position: relative;
  display: grid;
  grid-row: 1/3;
  grid-column: 2/3;
  width: 100%;
  height: 100%;
  background-color: transparent;
  /* border: 2px solid rgba(220, 220, 220, 0.4); */
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.05);
  border: 5px solid white;
  border-radius: 25px;
  z-index: -1;
  .parent {
    display: block;
    justify-content: center;
  }
  .date {
    display: block;

    padding-top: 7px;
    color: rgba(100, 120, 220, 1);
  }
  .sideDate {
    display: block;
  }
`
const DayGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  border: none;
  height: 90%;
  overflow-y: scroll;
  overscroll-behavior: contain;
`

const SingleDay = props => (
  <DayView>
    <div className="parent">
      <h5 className="date">{moment(props.date.toString()).format('dddd')}</h5>
      <hr />{' '}
      <p className="sideDate">
        {moment(props.date.toString()).format('MMMM Do YYYY')}
      </p>
    </div>
    <DayGrid>
      <div />
    </DayGrid>
  </DayView>
)

export default SingleDay
